

var tml = gsap.timeline({ defaults: { duration: 1 } })
tml.fromTo("body", { opacity: 0, backgroundColor: 'black' }, { opacity: 1, ease: "Power1.easeInOut", delay: 1 },)
    .fromTo(".nav_component", { opacity: 0, y: -100 }, { opacity: 1, y: 0, stagger: 0.1, ease: "Power1.easeInOut" }, "-=0.2")
    .fromTo(".nav_logo", { rotate: 0 }, { rotate: 360, ease: "Power1.easeInOut" }, "-=0.6")
    .fromTo(".header_wrapper", { opacity: 0, x: 50 }, { opacity: 1, ease: "Power1.easeInOut", x: 0 }, "-=0.8")
    .fromTo(".header_lottie-component", { opacity: 1, y: 1500, }, { opacity: 1, ease: "Power1.easeInOut", y: 0 },)
    .fromTo(".sup", { color: 'white', }, { color: '#F95A5A', ease: "Power2.easeInOut", y: 0, stagger: 0.01 },)

// .to(".sup", { "clip-path": "polygon(100% 0%, 0% 0%, 0% 125%, 100% 100%)",
// x: 0,
// duration: 1.8,})


let laptl = gsap.timeline({ defaults: { duration: 1 } })

laptl.fromTo(".course-wyn_animation-text-left", { x: 60, }, { x: 0, ease: "Power1.easeInOut", }, "-=0.10")
    .fromTo(".course-wyn_animation-text-right", { x: -60, }, { x: 0, ease: "Power1.easeInOut", }, "-=0.9")
    .fromTo(".course-wyn_midi-image", { opacity: 0, }, { opacity: 1, ease: "Power1.easeInOut", }, "-=0.9")
// .fromTo(".course-wyn_midi-title-mobile", { opacity:0,y:50, }, { opacity:1,y:0, ease: "Power1.easeInOut", }, "-=0.9")

ScrollTrigger.create({
    trigger: '.course-wyn_animation-text-left',
    start: "top 50%",
    end: "+=500",
    animation: laptl,
    // scrub: true, //enterback
    // events: onEnter onLeave onEnterBack onLeaveBack
    // toggleActions: "restart play reverse reset"
});


